<template>
  <div></div>
</template>
<style lang="scss" scoped>

</style>
<script>
  import {rebate_jump_to_taobao} from './popups.js'
  const remind = [
    '小贴士：聚划算商品无返利哦',
    '小贴示：虚拟商品无返利哦',
    '小贴士：返利只计算实付款金额哦',
    '小贴士：退货会扣除已发放的返利',
    '小贴士：货到付款的订单无返利哦'
  ]

  export default {
    mounted () {
      this.goodsName = this.$route.params.goodsName
      this.keyId = this.$route.params.keyId || ''
      let start = 3
      let index = Math.floor((Math.random() * (remind.length)))
      let popup = $popup.fromTemplate(
        rebate_jump_to_taobao.template(start, remind[index]),
        rebate_jump_to_taobao.options
      )
      this.countdownTimer = setInterval(_ => {
        let countDown = document.querySelector('#countDowm')
        start--
        if (start < 1) {
          clearInterval(this.countdownTimer)
          location.href = '/v4/rebate/redirect?keywords=' + this.goodsName + '&key_id=' + this.keyId
          popup.hide()
        }
        countDown.innerHTML = start
      }, 1000)
      popup.show()
    },

    beforeDestroy () {
      clearInterval(this.countdownTimer)
    }
  }
</script>



